.app {
  display: flex;
  flex-direction: row;
  min-height: 100vh;

  .info {
    flex: 1;
    display: flex;
    flex-direction: column;
    font-size: 20px;
    padding: 40px;
    flex-grow: 0;
    min-width: 500px;
    max-width: 100vw;
    z-index: 1;
    //justify-content: center;

    .parts {
      .part {
        display: block;
        margin-top: 20px;

        &.italic {
          font-style: italic;
        }

        &.links {
          flex: 1;
          align-content: flex-end;
          align-items: flex-end;
          display: flex;
          align-self: center;
          margin-top: 40px;

          >a {
            margin-right: 20px;

            &:hover {
              opacity: 0.75;
            }

            >img {
              height: 40px;
              width: 40px;
            }
          }
        }
      }
    }
  }

  .background {
    overflow: hidden;
    flex: 1;
    position: relative;
    z-index: 0;

    .background-image {
      height: 100%;
      width: 100%;
      -webkit-animation-name: pulse;
      animation-name: pulse;
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      -webkit-animation-duration: 60s;
      animation-duration: 60s;
      -webkit-animation-fill-mode: both;
      animation-fill-mode: both;
      animation-iteration-count: infinite;
      animation-timing-function: linear;
    }

    .rows {
      position: absolute;
      top: 0px;
      height: 100%;
      width: 100%;
    }

    .row {
      height: 50%;

      .column {
        height: 100%;
        width: 50%;
        display: inline-block;
        position: relative;

        .shape {
          height: 100%;
          width: 100%;
          background-color: #FFF;
          clip-path: polygon(0% 0%, 100% 0%, 0% 100%);
        }

        &.bottom-left {
          .shape {
            clip-path: polygon(0 0, 0% 100%, 100% 100%);
          }
        }

        &.top-left {
          .shape {
            clip-path: polygon(0 0, 0% 100%, 100% 0);
          }
        }

        &.top-right {
          .shape {
            clip-path: polygon(100% 100%, 0 0, 100% 0);
          }
        }

        &.bottom-right {
          .shape {
            clip-path: polygon(100% 100%, 0% 100%, 100% 0);
            ;
          }
        }
      }
    }
  }

  .image-wrapper {
    overflow: hidden;
    flex: 1;
    position: relative;
    z-index: 0;

    .image {
      height: 100%;
      -webkit-animation-name: pulse;
      animation-name: pulse;
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      -webkit-animation-duration: 60s;
      animation-duration: 60s;
      -webkit-animation-fill-mode: both;
      animation-fill-mode: both;
      animation-iteration-count: infinite;
      animation-timing-function: linear;
    }

    .overlay {
      width: 100%;
      top: 0px;
      left: 0px;
      height: 100%;
      position: absolute;
      background-color: #000;
      opacity: 0.5;
    }
  }
}

.hooks-main {
  width: 100vw;
  height: 100vh;
  position: fixed;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 1;
}

.b1,
.b2,
.b3 {
  position: absolute;
  will-change: transform;
  border-radius: 50%;
  background: lightpink;
  box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.5);
  opacity: 0.6;
  pointer-events: none;
}

.b1 {
  width: 170px;
  height: 170px;
}

.b2 {
  width: 350px;
  height: 350px;
}

.b3 {
  width: 200px;
  height: 200px;
}

.b1::after,
.b2::after,
.b3::after {
  content: '';
  position: absolute;
  top: 50px;
  left: 50px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.5);
}

.b2::after {
  top: 70px;
  left: 70px;
  width: 70px;
  height: 70px;
}

.b3::after {
  top: 50px;
  left: 50px;
  width: 50px;
  height: 50px;
}

.hooks-filter {
  position: absolute;
  width: 100%;
  height: 100%;
  filter: url('#goo');
  overflow: hidden;
}

@keyframes pulse {
  0% {
    -webkit-transform: scale(1.25) rotate(0deg);
  }

  50% {
    -webkit-transform: scale(1.75) rotate(20deg);
  }

  100% {
    -webkit-transform: scale(1.25) rotate(0deg);
  }
}

@media only screen and (max-width: 1000px) {
  .app {
    flex-direction: column;

    .info {
      min-width: auto;
    }

    .background {
      height: 100vh;

      .background-image {
        height: 100vh;
      }
    }

    .hooks-main {
      display: none;
    }
  }
}